.navbar-logo {
  font-family: 'Playfair Display', serif;  /* Koristi lep, elegantan font */
}

.navbar-title {
  color: #FFD700;  /* Zlatna boja za upečatljivost */
  font-size: 2rem;  /* Veći font */
  letter-spacing: 1.5px;  /* Razmak između slova za eleganciju */
}

.navbar {
  background-color: #007BFF;  /* Plava pozadina */
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
}

.navbar-links a {
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 500;
  margin: 0 1rem;
  transition: color 0.3s ease;
}

.navbar-links a:hover {
  color: #FFD700;
}

.navbar-links {
  list-style: none;
  display: flex;
  gap: 2rem;
}
