/* Motivation.css */
.motivation-container {
  padding: 4rem 2rem;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.motivation-title {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 1.5rem;
}

.motivation-content {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 2rem;
  line-height: 1.6;
}

.motivation-container p {
  margin-bottom: 1.5rem;
}
