/* Contact.css */
.contact-container {
  padding: 4rem 2rem;
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.contact-title {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 1rem;
}

.contact-description {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 2rem;
}

.contact-info p {
  font-size: 1.2rem;
  margin: 1rem 0;
}

.contact-info a {
  color: #007BFF;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.contact-footer {
  margin-top: 3rem;
  font-size: 1.2rem;
  color: #555;
}
