/* Projects.css */
.projects-container {
  padding: 4rem 2rem;
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
}

.projects-heading {
  font-size: 2.5rem;
  color: #007BFF;
  margin-bottom: 2rem;
  text-transform: uppercase;
}

.project-item {
  background-color: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-item:hover {
  transform: translateY(-5px);
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.15);
}

.project-title {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 1rem;
}

.project-description {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 1.5rem;
}

.project-link {
  color: #007BFF;
  font-weight: bold;
  text-decoration: none;
}

.project-link:hover {
  text-decoration: underline;
}
